import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    page: 1, //
	pageSize:10,
	ddPage:1,//
	ddPageSize:10,
	mdPage:1,//
	mdPageSize:10,
	mdgmShow:false,//
	yqhyPage:1,//
	yqhyPageSize:10,
	yjPage:1,//
	yjPageSize:10,
	yyjlPage:1,//
	yyjlPageSize:10,
	txjlPage:1,//
	txjlPageSize:15,
	shoucangPage:1,//
	shoucangPageSize:10,
  },
  mutations: {
    page(state, str) {
      state.page = str;
    },
	ddPage(state, str) {
	  state.ddPage = str;
	},
	mdPage(state, str) {
	  state.mdPage = str;
	},
	mdgmShow(state, str) {
	  state.mdgmShow = str;
	},
	yqhyPage(state, str) {
	  state.yqhyPage = str;
	},
	yjPage(state, str) {
	  state.yjPage = str;
	},
	yyjlPage(state, str) {
	  state.yyjlPage = str;
	},
	txjlPage(state, str) {
	  state.txjlPage = str;
	},
	shoucangPage(state, str) {
	  state.shoucangPage = str;
	},
  },
  actions: {

  },
});